// import { useState, useEffect } from 'react';
import AdminFamilyItem from './FamilyItem';

const AdminFamilyList = ({ families }) => {
  return (
    <div>
      <table className="activity-list">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th>-</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {families.map((family) => {
            return <AdminFamilyItem key={family.id} family={family} />;
          })}
        </tbody>
      </table>
    </div>
  );
};

export default AdminFamilyList;
