// import { useState, useEffect } from 'react';
import AdminActivityItem from './ActivityItem';

const AdminActivityList = ({ activities }) => {
  return (
    <div>
      <table className="activity-list">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th> </th>
            <th>  ()</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {activities.map((activity) => {
            return <AdminActivityItem key={activity.id} activity={activity} />;
          })}
        </tbody>
      </table>
    </div>
  );
};

export default AdminActivityList;
