import YouTube from 'react-youtube';
import { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { adminActivityService } from '../../services/admin/activity-service';
import { translateService } from '../../services/admin/translate-service';

export function AdminActivityDetail() {
  const { activityId } = useParams();
  const [activity, setActivity] = useState({});

  function youTubeGetID(url){
    url = url.split(/(vi\/|v%3D|v=|\/v\/|youtu\.be\/|\/embed\/)/);
    return undefined !== url[2]?url[2].split(/[^0-9a-z_\-]/i)[0]:url[0];
  }

  const loadActivity = () => {
    adminActivityService.getActivityById(activityId).then((activity) => setActivity(activity));
  };

  useEffect(() => {
    loadActivity()
  }, [activityId]);

  return (
    <>
      <Link to="/admin/activities" className='link'> </Link>
      {console.log(activity)}
      {Object.keys(activity).length === 0 && <p>Loading...</p>}
      {Object.keys(activity).length > 0 && (
        <div className='admin-activity-detail'>
            <h2>{activity.name}</h2>
            <p><span>:</span> {activity.description}</p>
            <p><span> :</span> {translateService.getTranslatedMediaType(activity.mediaType)}</p>
            <p><span> / :</span> {activity.data}</p>
            <p><span>   :</span> {activity.imageUrl}</p>
            {activity.imageUrl && <img src={activity.imageUrl} alt={activity.name} className="image-preview" /> }
            <p><span>:</span> {activity.trainer}</p>
            <p><span>   ():</span> {activity.durationInSeconds}</p>
            <p><span>:</span> {translateService.getTranslatedParticipantsType(activity.participantsType)}</p>
            <p><span> :</span> {translateService.getTranslatedTimeOfDayList(activity.recommendedTimeOfDay).join(' | ')}</p>
            <p><span> ?:</span> {JSON.parse(activity.active) ? <i className="uil uil-eye"> </i> : <i className="uil uil-eye-slash"> </i>}</p>
            {activity.mediaType === 'VIDEO' && <div className='player'>
                <YouTube videoId={youTubeGetID(activity.data)} opts={{height: '390', width: '640'}} onReady={(event)=>{event.target.pauseVideo()}} />
                </div>
            }
        </div>
      )}
    </>
  );
}
