import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { adminRegisterService } from '../../services/admin/register-service';
import AdminFamilyList from '../../cmps/admin/FamiliyList';

const AdminUsers = () => {
  const [families, setFamilies] = useState([]);
  const navigate = useNavigate();

  const loadFamilies = () => {
    adminRegisterService.getFamilies().then((families) => setFamilies(families));
  };

  useEffect(() => {
    loadFamilies();
  }, []);

  return (
    <div className="admin-activities">
      {console.log('families', families)}
      <h3 className="activities-title"> </h3>
      <button onClick={() => navigate('/admin/families/edit')}>
         
      </button>
      {families.length === 0 ? (
        <p> ...</p>
      ) : (
        <AdminFamilyList families={families} />
      )}
    </div>
  );
};

export default AdminUsers;