import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { adminActivityService } from '../../services/admin/activity-service';
import AdminActivityList from '../../cmps/admin/ActivityList';

const AdminActivities = () => {
  const [activities, setActivities] = useState([]);
  const navigate = useNavigate();

  const loadActivities = () => {
    // adminActivityService.query().then((res) => setActivities(res.activities));
    adminActivityService.query().then((activities) => setActivities(activities));
  };

  useEffect(() => {
    loadActivities();
  }, []);

  return (
    <div className="admin-activities">
      {console.log('activities', activities)}
      <h3 className="activities-title"> </h3>
      <button onClick={() => navigate('/admin/activities/edit')}>
         
      </button>
      {activities.length === 0 ? (
        <p> ...</p>
      ) : (
        <AdminActivityList activities={activities} />
      )}
    </div>
  );
};

export default AdminActivities;


