import { useNavigate } from 'react-router-dom';

const AdminFamilyItem = ({ family }) => {

  const navigate = useNavigate()

  return (
    <tr className='admin-activity-item'>
        <td onClick={()=>navigate(`/admin/families/edit/${family.id}`)}><i className="uil uil-edit"></i></td>
        {/* NOT <td><Link to={`/admin/families/${family.id}`}><i className="uil uil-file-info-alt"></i></Link></td> */}
        <td onClick={()=>navigate(`/admin/families/${family.id}`)}>
            <i className="uil uil-file-info-alt"></i>
        </td>
        <td>{family.imageUrl && <img src={family.imageUrl} alt='' style={{maxWidth: "100px", maxHeight: "50px", objectFit: "contain"}} /> }</td>
        <td>{family.name}</td>
        <td>{family.primaryEmail}</td>
        <td>{family.password}</td>
        {/* <td>{JSON.parse(family.active) ? <i className="uil uil-eye"></i> : <i className="uil uil-eye-slash"></i>}</td> */}
    </tr>
  );
};

export default AdminFamilyItem;
