import { Component } from 'react';
import { adminActivityService } from '../../services/admin/activity-service';
import withNavigation from '../../cmps/withNavigation';
import { Link } from 'react-router-dom';

class AdminActivityAdd extends Component {
  state = {
    activity: null
  };
  
  componentDidMount() {
    const { activityId } = this.props.params;
    if (activityId) {
      adminActivityService
        .getActivityById(activityId)
        .then((activityToEdit) => {
          this.setState({ activity: activityToEdit });
        });
    } else {
      const emptyActivity = adminActivityService.getEmptyActivity()
      this.setState({ activity: emptyActivity })
    }
  }

  inputHandler = (event) => {
    const { name } = event.target;
    let value;
    if (event.target.type === 'number'){
    value = +event.target.value;
    this.setState((prevState) => ({
      activity: { ...prevState.activity, [name]: value },
    })) }
    else if (event.target.type !== 'checkbox') {
      value = event.target.value;
      this.setState((prevState) => ({
        activity: { ...prevState.activity, [name]: value },
      }));
    } else if (name === 'active') {
      this.setState((prevState) => ({
        activity: {
          ...prevState.activity,
          [name]: !prevState.activity.active,
        },
      }));
    } else {
      const idx = this.state.activity[name].findIndex(
        (value) => value === event.target.value
      );
      const newValue = [...this.state.activity[name]];
      if (idx !== -1) newValue.splice(idx, 1);
      else newValue.push(event.target.value);
      this.setState((prevState) => ({
        activity: { ...prevState.activity, [name]: newValue },
      }));
    }
  };

  submitHandler = (ev) => {
    ev.preventDefault();
    adminActivityService
      .saveActivity(this.state.activity)
      .then(() => this.props.navigate('/admin/activities'));
  };

  render() {
    const { activityId} = this.props.params
    const { activity } = this.state
    console.log(activity)
    if (!activity) {
      return (
        <>
        <Link to="/admin/activities" className='link'> </Link>
        <p>Loading...</p>
        </>
      )
    }
    return (
      <div className="activity-form">
        <Link to="/admin/activities" className='link'> </Link>
        <h2 className="form-header">
          {activityId ? ' ' : ' '}
        </h2>
        <form onSubmit={this.submitHandler} className="activity-form-container">
          <div className="form-group">
            <label htmlFor="name">* :</label>
            <input
              type="text"
              name="name"
              id="name"
              autoComplete="off"
              onChange={this.inputHandler}
              value={activity.name}
              required
            ></input>
          </div>

          <div className="form-group">
            <label htmlFor="description">:</label>
            <br />
            <textarea
              name="description"
              id="description"
              rows="3"
              onChange={this.inputHandler}
              value={activity.description}
            ></textarea>
          </div>

          <div className="form-group">
            *  :
            <label>
              <input
                type="radio"
                name="mediaType"
                value="VIDEO"
                checked={activity.mediaType === 'VIDEO'}
                onChange={this.inputHandler}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="mediaType"
                value="AUDIO"
                checked={activity.mediaType === 'AUDIO'}
                onChange={this.inputHandler}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="mediaType"
                value="TEXT"
                checked={activity.mediaType === 'TEXT'}
                onChange={this.inputHandler}
              />
              
            </label>
          </div>

          <div className="form-group">
            <label htmlFor="data">* /:</label>
            <textarea
              name="data"
              id="data"
              rows={activity.mediaType === 'TEXT' ? '10' : '1'}
              onChange={this.inputHandler}
              value={activity.data}
            ></textarea>
          </div>

          <div className="form-group">
            <label htmlFor="data">    :</label>
            <textarea
              name="imageUrl"
              id="imageUrl"
              // rows="1"
              onChange={this.inputHandler}
              value={activity.imageUrl}
            ></textarea>
          </div>
          {activity.imageUrl && <img src={activity.imageUrl} alt={activity.name} className="image-preview" /> }

          <div className="form-group">
            <label htmlFor="name">:</label>
            <input
              type="text"
              name="trainer"
              id="trainer"
              autoComplete="off"
              onChange={this.inputHandler}
              value={activity.trainer}
            ></input>
          </div>

          {/* Minutes and seconds display */}
          <div className="form-group">
            <label htmlFor="name">*    ():</label>
            <input
              type="number"
              name="durationInSeconds"
              id="durationInSeconds"
              onChange={this.inputHandler}
              value={activity.durationInSeconds}
            ></input>
          </div>

          <div className="form-group">
            * :
            <label htmlFor="kids">
              <input
                type="radio"
                name="participantsType"
                value="KIDS"
                onChange={this.inputHandler}
                checked={activity.participantsType === 'KIDS'}
              />
              
            </label>
            <label htmlFor="adults">
              <input
                type="radio"
                name="participantsType"
                value="ADULTS"
                onChange={this.inputHandler}
                checked={activity.participantsType === 'ADULTS'}
              />
              
            </label>
            <label htmlFor="family">
              <input
                type="radio"
                name="participantsType"
                value="ENTIRE_FAMILY"
                onChange={this.inputHandler}
                checked={
                  activity.participantsType === 'ENTIRE_FAMILY'
                }
              />
               
            </label>
          </div>

          <div className="form-group">
            *  :
            <label htmlFor="morning">
              <input
                type="checkbox"
                name="recommendedTimeOfDay"
                id="morning"
                value="MORNING"
                onChange={this.inputHandler}
                checked={activity.recommendedTimeOfDay.includes('MORNING')}
              />
              
            </label>
            <label htmlFor="afternoon">
              <input
                type="checkbox"
                name="recommendedTimeOfDay"
                id="afternoon"
                value="AFTERNOON"
                onChange={this.inputHandler}
                checked={activity.recommendedTimeOfDay.includes('AFTERNOON')}
              />
               
            </label>
            <label htmlFor="evening">
              <input
                type="checkbox"
                name="recommendedTimeOfDay"
                id="evening"
                value="EVENING"
                onChange={this.inputHandler}
                checked={activity.recommendedTimeOfDay.includes('EVENING')}
              />
              
            </label>
            <label htmlFor="night">
              <input
                type="checkbox"
                name="recommendedTimeOfDay"
                id="night"
                value="NIGHT"
                onChange={this.inputHandler}
                checked={activity.recommendedTimeOfDay.includes('NIGHT')}
              />
              
            </label>
          </div>

          <div className="form-group">
            <label htmlFor="active"> ? </label>
            <input
              type="checkbox"
              name="active"
              id="active"
              checked={activity.active}
              onChange={this.inputHandler}
            />
          </div>

          <button>/</button>
        </form>
      </div>
    );
  }
}

export default withNavigation(AdminActivityAdd);
