import { Component } from 'react';
import { adminRegisterService } from '../../services/admin/register-service';
import withNavigation from '../../cmps/withNavigation';
import { Link } from 'react-router-dom';

class AdminFamilyAdd extends Component {
  state = {
    family: null
  };
  
  componentDidMount() {
    const { familyId } = this.props.params;
    if (familyId) {
      adminRegisterService
        .getFamilyById(familyId)
        .then((familyToEdit) => {
          this.setState({ family: familyToEdit });
        });
    } else {
      const emptyFamily = adminRegisterService.getEmptyFamily()
      this.setState({ family: emptyFamily })
    }
  }

  inputHandler = (event) => {
    const { name } = event.target;
    let value = event.target.value;
    this.setState((prevState) => ({
      family: { ...prevState.family, [name]: value },
    }));
  };

  submitHandler = (ev) => {
    ev.preventDefault();
    adminRegisterService
      .addFamily(this.state.family)
      .then(() => this.props.navigate('/admin/users'));
  };

  render() {
    const { familyId} = this.props.params
    const { family } = this.state
    console.log(family)
    if (!family) {
      return (
        <>
        <Link to="/admin/users" className='link'> </Link>
        <p>Loading...</p>
        </>
      )
    }
    return (
      <div className="activity-form">
        <Link to="/admin/users" className='link'> </Link>
        <h2 className="form-header">
          {familyId ? ' ' : ' '}
        </h2>
        <form onSubmit={this.submitHandler} className="activity-form-container">
          <div className="form-group">
            <label htmlFor="name">* :</label>
            <input
              type="text"
              name="name"
              id="name"
              autoComplete="off"
              onChange={this.inputHandler}
              value={family.name}
              required
            ></input>
          </div>

          <div className="form-group">
            <label htmlFor="email">* -:</label>
            <input
              type="email"
              name="primaryEmail"
              id="email"
              autoComplete="off"
              onChange={this.inputHandler}
              value={family.primaryEmail}
              required
            ></input>
          </div>

          <div className="form-group">
            <label htmlFor="password">* :</label>
            <input
              type="password"
              name="password"
              id="password"
              autoComplete="off"
              onChange={this.inputHandler}
              value={family.password}
              required
            ></input>
          </div>

          <div className="form-group">
            <label htmlFor="data">  :</label>
            <textarea
              name="imageUrl"
              id="imageUrl"
              // rows="1"
              onChange={this.inputHandler}
              value={family.imageUrl}
            ></textarea>
          </div>
          {family.imageUrl && <img src={family.imageUrl} alt={family.name} className="image-preview" /> }

          <button>/ </button>
        </form>
      </div>
    );
  }
}

export default withNavigation(AdminFamilyAdd);
