import { translateService } from '../../services/admin/translate-service';
import { useNavigate } from 'react-router-dom';

const AdminActivityItem = ({ activity }) => {

  const navigate = useNavigate()

  return (
    <tr className='admin-activity-item'>
        <td onClick={()=>navigate(`/admin/activities/edit/${activity.id}`)}><i className="uil uil-edit"></i></td>
        {/* <td><Link to={`/admin/activities/${activity.id}`}><i className="uil uil-file-info-alt"></i></Link></td> */}
        <td onClick={()=>navigate(`/admin/activities/${activity.id}`)}>
            <i className="uil uil-file-info-alt"></i>
        </td>
        <td>{activity.imageUrl && <img src={activity.imageUrl} alt='' style={{maxWidth: "100px", maxHeight: "50px", objectFit: "contain"}} /> }</td>
        <td>{activity.name}</td>
        {/* <td>{activity.participantsType}</td> */}
        {/* <td>{activity.recommendedTimeOfDay.join(' | ')}</td> */}
        <td>{translateService.getTranslatedParticipantsType(activity.participantsType)}</td>
        <td>{translateService.getTranslatedTimeOfDayList(activity.recommendedTimeOfDay).join(' | ')}</td>
        <td>{activity.durationInSeconds}</td>
        <td>{JSON.parse(activity.active) ? <i className="uil uil-eye"></i> : <i className="uil uil-eye-slash"></i>}</td>
    </tr>
  );
};

export default AdminActivityItem;
