import { httpService } from '../http-service'

const BASE_URL = 'bo/register'

export const adminRegisterService = {
    addFamily,
    addMember,
    getFamilies,
    // getFamilyById, //returns family + members
    // udpateFamily,
    // deleteFamily,
    // updateMember,
    // deleteMember,
    getEmptyFamily,
    getEmptyMember
}


async function addFamily(family) {
    console.log('add family:', family)
    try {
        const savedFamily = await httpService.post(`${BASE_URL}/family`, family)
        return savedFamily
    } catch (err) {
        console.log('Error adding family:', err)
    }
}

async function addMember(member) {
    console.log('add member:', member)
    try {
        const savedFamily = await httpService.post(`${BASE_URL}/member`, member)
        return savedFamily
    } catch (err) {
        console.log('Error adding member:', err)
    }
}

