async function getFamilies() {
    console.log('getFamilies family service')
    try {
        const families = await httpService.get(`${BASE_URL}/family`)
        console.log('getFamilies families:', families)
        return families.families
    } catch (err) {
        console.log('getFamilies had an err:', err)
    }
}

