function getEmptyFamily() {
    return {
        name: '',
        primaryEmail: '',
        password: '',
        imageUrl: ''
    }
}

function getEmptyMember() {
    return {
        name: '',
        email: '',
        role: null, //parent / child
        yearOfBirth: null, //int
        imageUrl: '',
        familyId: null
    }
}


    