import { httpService } from '../http-service'

const BASE_URL = 'bo/activities'

export const adminActivityService = {
    query,
    getActivityById,
    saveActivity,
    getEmptyActivity
}

async function query() {
    console.log('query admin service')
    try {
        const activities = await httpService.get(BASE_URL)
        console.log('query activities:', activities)
        return activities.activities
    } catch (err) {
        console.log('query had an err:', err)
    }
}

async function getActivityById(id) {
    console.log('get by id:', id)
    try {
        const activity = await httpService.get(`${BASE_URL}/${id}`)
        return activity
    } catch (err) {
        console.log('Error:', err)
    }
}

async function saveActivity(activity) {
    try {
        if (activity.id) {
            const savedActivity = await httpService.put(BASE_URL, activity)
            return savedActivity
        } else {
            const savedActivity = await httpService.post(BASE_URL, activity)
            return savedActivity
        }
    } catch (err) {
        console.log('Error:', err)
    }
}

function getEmptyActivity() {
    return {
        name: '',
        description: '',
        data: '',
        mediaType: '',
        participantsType: '',
        trainer: '',
        durationInSeconds: 0,
        imageUrl: '',
        recommendedTimeOfDay: [],
        active: true
    }
}
